input[type=text], select {
  width: 90%;
  padding: 12px 20px;
  margin: auto;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  text-align: center;
  font-size: 16px;


}

input[type=submit] {
  width: 90%;
  background-color: #4a76a8;
  color: white;
  padding: 14px 20px;
  margin: auto;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;

}

input[type=submit]:hover {
  background-color: #4c83b2;
}

